import React from 'react';
import { Breadcrumb } from 'antd';


const Breadcrumbs = props => {
    return (
        <Breadcrumb style={{ margin: '16px 0' }}>
            { props.crumb.map(item => (
                <Breadcrumb.Item key={item}>{item}</Breadcrumb.Item>
            ))}
        </Breadcrumb>
    );
};

export default Breadcrumbs;