import React from 'react';
import Breadcrumbs from '../Common/Breadcrumbs';
import { Row, Col, Card, Table, Tag, Space } from 'antd'
import { Bar, Doughnut, Line } from 'react-chartjs-2';

import data from '../../mocks/chartData'

const Dashboard = () => {
    const path = [
        'Home'
    ]

    const Layout = {
        xl: { span: 8 },
        lg: { span: 8 },
        md: { span: 12 },
        sm: { span: 24 },
        xs: { span: 24 },
    };

    const options = {
        scales: {
            yAxes: [
                {
                    ticks: {
                        beginAtZero: true,
                    },
                },
            ],
        },
        maintainAspectRatio: false,
    }


    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
            key: 'name',
            render: text => <a>{text}</a>,
            sorter: true,
        },
        {
            title: 'Age',
            dataIndex: 'age',
            key: 'age',
            sorter: true,
        },
        {
            title: 'Address',
            dataIndex: 'address',
            key: 'address',
            sorter: true,
        },
        {
            title: 'Tags',
            key: 'tags',
            dataIndex: 'tags',
            render: tags => (
                <>
                    {tags.map(tag => {
                        let color = tag.length > 5 ? 'geekblue' : 'green';
                        if (tag === 'Paid') {
                            color = 'volcano';
                        }
                        return (
                            <Tag color={color} key={tag}>
                                {tag.toUpperCase()}
                            </Tag>
                        );
                    })}
                </>
            ),
        },
        {
            title: 'Action',
            key: 'action',
            render: (text, record) => (
                <Space size="middle">
                    <a>Message {record.name}</a>
                    <a>Delete</a>
                </Space>
            ),
        },
    ];

    const tabledata = [
        {
            key: '1',
            name: 'John Brown',
            age: 32,
            address: 'New York No. 1 Lake Park',
            tags: ['New', 'Delivered'],
        },
        {
            key: '2',
            name: 'Jim Green',
            age: 42,
            address: 'London No. 1 Lake Park',
            tags: ['Paid'],
        },
        {
            key: '3',
            name: 'Joe Black',
            age: 32,
            address: 'Sidney No. 1 Lake Park',
            tags: ['Old', 'Processing'],
        },
    ];


    return (
        <div>
            <Breadcrumbs crumb={path} />
            <Row gutter={16} className='charts'>
                <Col {...Layout}>
                    <Card title="Sales" bordered={false}>
                        <Bar
                            height={300}
                            width={550}
                            data={data.data1}
                            options={options}
                        />
                    </Card>
                </Col>
                <Col  {...Layout}>
                    <Card title="Products" bordered={false}>
                        <Doughnut data={data.data2} options={{ maintainAspectRatio: false, }} />
                    </Card>
                </Col>
                <Col  {...Layout}>
                    <Card title="Users" bordered={false}>
                        <Line data={data.data3} options={options} height={150}
                            width={590} />
                    </Card>
                </Col>
            </Row>
            <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                <Table columns={columns} dataSource={tabledata} />
            </div>
        </div>
    );
};

export default Dashboard;